import { Component, OnInit,Input,AfterViewInit,AfterContentInit, ViewChild, ElementRef } from '@angular/core';
import { BackendService } from '../backend.service';
import { Router } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-view-dash',
  templateUrl: './view-dash.component.html',
  styleUrls: ['./view-dash.component.css']
})
export class ViewDashComponent implements AfterViewInit {
  @ViewChild('treeViewWidget',{static:false,read:ElementRef}) treeViewwidget:ElementRef;

  public scrollRight(): void {

    this.treeViewwidget.nativeElement.scrollTo({right:(this.treeViewwidget.nativeElement.scrollRight + 150)});
    // this.treeViewwidget.nativeElement.scrollTo({ left: (this.treeViewwidget.nativeElement.scrollLeft + 150), behavior: 'smooth' });
  }

  public scrollLeft(): void {
    this.treeViewwidget.nativeElement.scrollTo({ right: (this.treeViewwidget.nativeElement.scrollLeft - 150), behavior: 'smooth' });
  }
  @Input() empArr;
  orgaArr = [];
  employees = [];
  loggedUserData;
  isLoading: Boolean = true;
  constructor(
    private backend:BackendService,
    private router:Router,
    private cookies:CookieService
    ) { 

    this.backend.getOrgaStructure()
    .subscribe(
      data => {
        this.orgaArr = data;
      }
    );

   // 

  } //Constructor ends here

 
 

  ngOnChanges() {
    if (this.empArr.length > 0) {
      this.isLoading = false;
      this.employees = this.empArr;
      console.log('this.employees .ngOnChanges => ', this.empArr);
    }
  }

  ngAfterViewInit(){

    
    if(!this.backend.checkSession()){
      this.router.navigate(['/home']);
      return false;
    }else{
      this.loggedUserData = this.backend.getUserFromLocalStorage();

      this.loggedUserData.userType == 'admin' 
        ? this.employees =  this.backend.getTreeData() 
        : this.employees =  this.backend.getTreeData();
      //this.employees =  this.backend.getTreeData();

      //this.loggedUserData.userType == 'user'?this.loggedUserData.userId:null
    }
   // this.loadUserData()
   setTimeout(() => {
    
    let index = this.employees.findIndex(dt=> dt.uid == this.loggedUserData.userId);
    console.log("Index Number " , index );
    //this.employees = this.employees.splice(index, this.employees.length);


    
    //console.log(this.employees);
   }, 1000); 
} //ngViewAfterInit

scrollPage(dir){
  this.treeViewwidget.nativeElement.scrollTo({ left: (this.treeViewwidget.nativeElement.scrollRight - 150), behavior: 'smooth' });
}




  logout(){
    this.cookies.delete('auth');
    this.router.navigate(['/home']);
  } //Logout ends here



  loadUserData(){
    this.employees = [];
    this.backend.getProfile()
    .subscribe((res)=>{

      let tempArray = res['data'];
      //this.employees = tempArray.filter(dt=>dt.reference == this.loggedUserData.empid);
      tempArray.forEach(data => {
        console.log("Server-----------------------------data",data)
        // let filterData = tempArray.filter(dt=>{
        //   return dt.reference == this.loggedUserData.empid
        // })

        this.employees = this.filterUserData(tempArray);
         // this.empArra.push();
        this.orgaArr = this.employees.filter(dt => dt.empid == data.reference)
        console.log("Orga Structre Data", this.orgaArr);
        const empLen = this.employees.length;
        const lastEmpId = empLen == 0 ? 0 : this.employees[empLen - 1].empid;
        //const desgId    = this.employees[empLen - 1].empdesgid;
        const obj = {
        "empid": data.empid,
        "uid"  : data.userId,
        "empname": data.username,
        "parentid": this.orgaArr.length > 0 ? Number(this.orgaArr[0].parentid + 1) : 0,
        "empdesgid": 2,
        // tslint:disable-next-line:radix
        "supervisorid": data.reference ? parseInt(data.reference):0,
        "empdesgname": ""
      };
        console.log("User Object -----------",obj);
        this.employees.push(obj); 
        
      }); 
     


    }) //Subscribe method ends here.
    } //Load user data ends here.


    filterUserData(arr){
      let tempArr = []
      //tempArr.push()
      tempArr = arr.filter(dt=>dt.emp == this.loggedUserData.empid);
      // arr.forEach(data => {
      //   console.log("Server-----------------------------data",data)
      //   let filterData = arr.filter(dt=>{
      //     return dt.reference == this.loggedUserData.empid
      //   })

      //   this.employees.push(filterData);
      return tempArr;
    }

} //Main Class ends here
