import { Component, OnInit } from '@angular/core';
import { BackendService } from '../backend.service';
import { CookieService } from 'ngx-cookie-service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-nav-bar-child',
  templateUrl: './nav-bar-child.component.html',
  styleUrls: ['./nav-bar-child.component.css']
})
export class NavBarChildComponent implements OnInit {
  isUserLogged = false;
  isAdmin = false;
  constructor(

    private backend:BackendService,
    private cookies : CookieService,
    private router : Router

  ) { }

  ngOnInit() {

      this.backend.checkSession() ? this.isUserLogged = true : this.isUserLogged = false;
      let user = this.backend.getUserFromLocalStorage()
      user.userType == 'admin' ? this.isAdmin = true : this.isAdmin = false;
  }


  logout(){
    this.isUserLogged = false;
    this.cookies.delete('auth');
    localStorage.removeItem('userData');
    this.router.navigate(['/home']);
  } //Logout ends here

}
